.checkbox-form {
  /*display:inline-block;*/
  margin-top: 20px;
  align-items: flex-end;
  display: flex;
  flex-direction: column;
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox {
  cursor: pointer;
  display: flex;
  color: #707070;
  font-size: 12px;
  font-family: " Open Sans", sans-serif;
  font-weight: 400;
  max-width: 450px;
}

.checkbox span {
  height: 22px;
  width: 22px;
  flex-shrink: 0;
  /*flex-basis: 60px;*/
  border: 1px solid #D1D1D1;
  cursor: pointer;
  margin-right: 15px;
  border-radius: 8px;
  color: black;
  content: "";
}

.checkbox {
  display: inline-flex;
  cursor: pointer;
  position: relative;
}

/*    margin-left:16px;*/
/*    margin-top: 24px;*/
/*}*/
.checkbox > span {
  color: #D1D1D1;
  /*padding-left: 30px;*/
  display: block;
}

.checkbox > input {
  height: 22px;
  width: 22px;
  -webkit-appearance: none;
  /*-moz-appearance: none;*/
  /*-o-appearance: none;*/
  appearance: none;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  outline: none;
  transition-duration: 0.3s;
  /*background-color: #41B883;*/
  cursor: pointer;
  display: block;
}

.checkbox > input:checked {
  border: 1px solid #D1D1D1;
  /*background-color: #D1D1D1;*/
}

.checkbox > input:checked + span::before {
  content: "✓";
  font-size: 2em;
  display: block;
  text-align: center;
  color: #08A6E4;
  position: absolute;
  left: 0.2em;
  /*top: 0.2em;*/
}

.checkbox > input:active {
  border: 2px solid #D1D1D1;
}

